*{
    box-sizing: border-box;
}
.Qoutes{
    width: 100%;
    background-color: #f6f8f5;
    padding: 70px;
}
.Qoutes__text{
    text-align: center;
max-width: 950px;
    font-size: 40px;
    margin: 0 auto;
    color: var(--global-color-2);
}
.Qoutes__avatar{
    display: block;
    margin: auto;
    width: 4%;
    margin-top: 30px;
}
.Qoutes__jane{
    margin-top: 20px;
    font-size: 13px;
    text-align: center;
    color: grey;
}
@media(max-width:481px){
    .Qoutes__text{
   
        font-size: 20px;
   
    }
    .Qoutes__jane{

        font-size: 15px;
 
    }
    .Qoutes__avatar{
       
        width: 10%;
        
    }
}